.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}
.portfolio__item {
  /* background: var(--color-bg-variant); */
  padding: 1.3rem;
  margin-top: 2px;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
  text-align: center;

  background: #232526;  
  background: linear-gradient(to left, #232526,#414345 ); 
  background: -webkit-linear-gradient(to left, #414345, #232526);  
 
  /* box-shadow:  9px 9px 7px 0 rgba(245, 140, 4, 0.5),
  -9px -9px 7px 0 rgba(6, 114, 247, 0.5), 
inset -5px -5px 4px 0 #f59403,
inset 5px 5px 4px 0 rgba(6, 114, 247, 0.5); */
}

.portfolio__item:hover {
  /* border-color: var(--color-primary-variant); */
  /* background: transparent; */
  box-shadow:  9px 9px 7px 0 rgba(245, 140, 4, 0.5),
  -9px -9px 7px 0 rgba(6, 114, 247, 0.5), 
inset -5px -5px 4px 0 #f59403,
inset 5px 5px 4px 0 rgba(6, 114, 247, 0.5);
}
.portfoli__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
  margin-top: 10px;
}
.portfolio__item h3 {
  margin: 0rem 0.2rem;
  color: var(--color-primary);
}
.portfolio__item h5 {
  padding-top: 10px;
  color: var(--color-light);
}
.portfolio__item-cta {
  display: flex;
  gap: 5px;
  margin-bottom: 1rem;
  margin-top: 15px;
}
.portfolio__item-stack {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
}

/* <-----------------------button animation --------------------> */

#btn_animation{
  width: max-content;
  display: inline-block;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
background: #171618;
border: none;
}

#btn_animation {
position: relative;
box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.4);
color: rgb(252, 187, 67);
text-decoration: none;



overflow: hidden;
}

#btn_animation span:nth-child(1) {
position: absolute;
top: 0;
right: 0;
width: 100%;
height: 3px;
background: linear-gradient(to right, #171618, #f59403);
animation: animate1 2s linear infinite;
}

@keyframes animate1 {
0% {
  transform: translateX(-90%);
}
100% {
  transform: translateX(90%);
}
}

#btn_animation span:nth-child(2) {
position: absolute;
top: 0;
right: 0;
height: 100%;
width: 3px;
background: linear-gradient(to bottom, #171618, #f59403);
animation: animate2 2s linear infinite;
animation-delay: 1s;
}
@keyframes animate2 {
0% {
  transform: translateY(-90%);
}
100% {
  transform: translateY(90%);
}
}

#btn_animation span:nth-child(3) {
position: absolute;
bottom: 0;
right: 0;
width: 100%;
height: 3px;
background: linear-gradient(to left, #171618, #f59403);
animation: animate3 2s linear infinite;
}

@keyframes animate3 {
0% {
  transform: translateX(90%);
}
100% {
  transform: translateX(-90%);
}
}

#btn_animation span:nth-child(4) {
position: absolute;
top: 0;
left: 0;
height: 90%;
width: 3px;
background: linear-gradient(to top, #171618, #f59403);
animation: animate4 2s linear infinite;
animation-delay: 1s;
}

@keyframes animate4 {
0% {
  transform: translateY(90%);
}
100% {
  transform: translateY(-90%);
}
}
#btn_animation:hover{
box-shadow:  3px 3px 5px 0 rgba(245, 140, 4, 0.5),
            -4px -4px 5px 0 rgba(6, 114, 247, 0.5), 
  inset -5px -5px 4px 0 #f59403,
  inset 5px 5px 4px 0 rgba(6, 114, 247, 0.5);
}


/* ...............Media queries(Medium Device)........................ */
@media screen and (max-width: 1250px) {
  .portfolio__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    justify-items: center;
  }
}

/* ...............Media queries(Small Device)........................ */
@media screen and (max-width: 600px) {
  .portfolio__container {
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    justify-items: center;
  }
  
.portfolio__item-stack {
  margin-top: 10px;
  display: grid;
  gap: 5px;
  grid-template-columns: repeat(3, 1fr);
}
}
