footer {
 
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
 
}
.foot{
   /* background-color: #4DB5FF; */
   /* background-image: url('../Doc/Background-image/osama_banner.jpg'); */
   background: #232526;  
   background: linear-gradient(to left, #232526,#414345 ); 
   background: -webkit-linear-gradient(to left, #414345, #232526); 
}
footer a {
  /* color: var(--color-bg); */
  color: white;
}
.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}
.footer__logo:hover{
  color: rgb(104, 101, 101);
}
.permalinks  a:hover{
   color: rgb(104, 101, 101);
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}
.footer__socials a {
  /* background: var(--color-bg); */
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  background: #870000;  
background: -webkit-linear-gradient(to right, #190A05, #870000);  
background: linear-gradient(to right, #190A05, #870000); 

}
.footer__socials a:hover {
  background: transparent;
  color: rgb(6, 247, 6);
  border-color: red;
}
.footer__copyrihght  {
  margin-bottom: 4rem;
 color: white;
  font-size: 13px;
}



