.container.contact__container{
    width: 50%;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:5%;
    justify-content: center;
   
 }
 .contact__options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
   
 }
 .contact__option{
     /* background: var(--color-bg-variant); */
     padding: 1.6rem;
     border-radius: 1.2rem;
     text-align: center;
     border: 1px solid transparent;
     /* transition: var(--transition); */
     background: #232526;  
   background: linear-gradient(to left, #232526,#414345 ); 
   background: -webkit-linear-gradient(to left, #414345, #232526); 
 
   box-shadow:  9px 9px 7px 0 rgba(245, 140, 4, 0.5),
   /* -9px -9px 7px 0 rgba(6, 114, 247, 0.5),  */
 inset -5px -5px 4px 0 #f59403,
 inset 5px 5px 4px 0 rgba(6, 114, 247, 0.5);
 }
 
 /* .contact__option:hover{ */
     /* background: transparent; */
     /* border-color: var(--color-primary-variant); */
 /* } */
 .contact__option-icon{
     font-size: 1.5rem;
     margin-bottom: 0.5rem;
 }
 .contact__option a {
 margin-top: 0.7rem;
 display: inline-block;
 font-size: 0.9rem;
 }
 .icon{
     padding-top: 5px;
 }
 .contact__option a:hover{
     color: rgb(13, 243, 32);
 }
 
 /* ...............Media queries(Medium Device)........................ */
 @media screen and (max-width: 1024px) {
    .container.contact__container{
        grid-template-columns: repeat(2,1fr);
        gap: 2rem;
    }
   }
   
   
   
   /* ...............Media queries(Small Device)........................ */
   @media screen and (max-width: 600px) {
    
     .container.contact__container{
        width: var(--container-width-sm);
        
     }
     
   }