header {
  height: 100vh;
  padding-top: 7rem;
  /* overflow: hidden; */
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ......CTA.... */
.cta {
  display: flex;
  margin-top: 2.5rem;
  gap: 1.2rem;
  justify-content: center;
}
/* .... Header Social........ */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}
/* .......Me.......... */
.me {
 
  width: 20rem;
  height: 20rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  overflow: hidden;
  /* border: 1px solid red; */
  /* border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px; */
  border-radius: 100%;
  /* border: 1px solid red; */
  /* border: 1px solid var(--color-primary); */
  border: 1px solid rgb(255, 153, 0);
  box-shadow: 0 5px 15px 0px rgba(255, 127, 8, 0.6);
	animation: float 6s ease-in-out infinite;
}


@keyframes float {
  
	0% {
		box-shadow: 0 5px 15px 0px rgba(19, 18, 18, 0.6);
	  transform: translateX(-48%);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(250, 132, 53, 0.2);
	  transform: translateX(-49%);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(250, 92, 1, 0.6);
    transform: translateX(-50%);
	}
}



/* ...............Media queries(Medium Device)........................ */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
    margin-bottom: 200px;
  }
}

/* ...............Media queries(Small Device)........................ */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials {
    display: none;
  }
  .scroll__down {
    display: none;
  }
}
