#btn{
    margin: 0;
	background: #171618;
	border: none;
	display: grid;
	place-items: center;
	
}

#btn {
	position: relative;
	box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.4);
	color: rgb(252, 187, 67);
	text-decoration: none;
	text-transform: uppercase;
	
	
	overflow: hidden;
}

#btn span:nth-child(1) {
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	height: 3px;
	background: linear-gradient(to right, #171618, #f59403);
	animation: animate1 2s linear infinite;
}

@keyframes animate1 {
	0% {
		transform: translateX(-90%);
	}
	100% {
		transform: translateX(90%);
	}
}

#btn span:nth-child(2) {
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	width: 3px;
	background: linear-gradient(to bottom, #171618, #f59403);
	animation: animate2 2s linear infinite;
	animation-delay: 1s;
}
@keyframes animate2 {
	0% {
		transform: translateY(-90%);
	}
	100% {
		transform: translateY(90%);
	}
}

#btn span:nth-child(3) {
	position: absolute;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 3px;
	background: linear-gradient(to left, #171618, #f59403);
	animation: animate3 2s linear infinite;
}

@keyframes animate3 {
	0% {
		transform: translateX(90%);
	}
	100% {
		transform: translateX(-90%);
	}
}

#btn span:nth-child(4) {
	position: absolute;
	top: 0;
	left: 0;
	height: 90%;
	width: 3px;
	background: linear-gradient(to top, #171618, #f59403);
	animation: animate4 2s linear infinite;
	animation-delay: 1s;
}

@keyframes animate4 {
	0% {
		transform: translateY(90%);
	}
	100% {
		transform: translateY(-90%);
	}
}
#btn:hover{
	box-shadow:  9px 9px 7px 0 rgba(245, 140, 4, 0.5),
              -9px -9px 7px 0 rgba(6, 114, 247, 0.5), 
    inset -5px -5px 4px 0 #f59403,
    inset 5px 5px 4px 0 rgba(6, 114, 247, 0.5);
}