.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}
.about__me {
  width: 90%;

  border-radius: 2rem;
  background: linear-gradient(
    40deg,
    transparent,
    /* var(--color-primary), */
    transparent
  );
  display: grid;
  place-items: center;
}
/* ......need some extra work over here */
.about__me-image {
  border-radius: 2rem;
  height: 100%;
  width: 100%;
  overflow: hidden;
 
  transition: var(--transition);
  
}
.about__me-image,img{
  height:100%;
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.about__card {
  /* background: var(--color-bg-variant); */
  /* background: #BA8B02;  
background: -webkit-linear-gradient(to right, #181818, #BA8B02);  
background: linear-gradient(to right, #181818, #BA8B02);  */

background: #870000;  
background: -webkit-linear-gradient(to right, #190A05, #870000);  
background: linear-gradient(to right, #190A05, #870000); 
box-shadow:  9px 9px 7px 0 #800000,
            /* -5px -5px 4px 0 rgba(255, 255, 255, 0.5),  */
  inset -9px -9px 7px 0 #bb0909,
  inset 5px 5px 4px 0 rgba(255, 255, 255, 0.5);



  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}
.about__card:hover {
  /* background: transparent; */
  /* border-color: var(--color-primary-variant); */
  cursor: default;
  /* box-shadow:  9px 9px 7px 0 #800000,
              -5px -5px 4px 0 rgba(255, 255, 255, 0.5), 
    inset -9px -9px 7px 0 #bb0909,
    inset 5px 5px 4px 0 rgba(255, 255, 255, 0.5); */
  
}
.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about__card h5 {
  font-size: 0.95rem;
}
.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}
.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}


/* ...............Media queries(Medium Device)........................ */
@media screen and (max-width: 1024px) {
  .about__container{
    grid-template-columns: 1fr;
    gap:0;
  }
  .about__me{
    width: 50%;
    margin:  2rem auto 4rem;
  }
  .about__content p{
    margin: 1rem 0 1.5rem;
  }
}



/* ...............Media queries(Small Device)........................ */
@media screen and (max-width: 600px) {
 .about__me{
   width: 65%;
   margin: 0 auto 3rem ;
 }
 .about__cards{
   grid-template-columns: 1fr 1fr;
   gap:1rem;

 }
 .about__content{
   text-align: center;
 }
 .about__content p{
   margin: 1.5rem 0;
 }
}