.experience__container {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 4rem;
}
.experience__container > div {

  /* background: var(--color-bg-variant); */
  padding: 2.4rem 2.4rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  /* transition: var(--transition); */
  background: #232526;  
  background: linear-gradient(to left, #232526,#414345 ); 
  background: -webkit-linear-gradient(to left, #414345, #232526);  
 
box-shadow:  9px 9px 7px 0 rgba(6, 114, 247, 0.5),
            /* -5px -5px 4px 0 rgba(255, 255, 255, 0.5),  */
  inset -5px -5px 5px 0 rgba(6, 114, 247, 0.5),
  inset 5px 5px 4px 0 rgba(179, 179, 179, 0.5);
}
.experience__container > div:hover {
  /* background: transparent; */
  border-color: var(--color-primary-variant);
  cursor: default;
}
.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}
.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
}
.experience__details {
  display: flex;
  gap: 1rem;
}

.experience__details>div>h4{
  font-size: 14px;
}

.experience__details-icon {
  margin-top: 6px;
  color: var(--color-primary);
}



/* ...............Media queries(Medium Device)........................ */
@media screen and (max-width: 1250px) {
  .experience__container {
    grid-template-columns: repeat(2, 1fr);
   
  }
  .experience__container > div {
    width: 100%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience__content {
    padding: 1rem;
  }
}

/* ...............Media queries(Small Device)........................ */
@media screen and (max-width: 600px) {
  .experience__container {
    grid-template-columns: repeat(1,1fr);
    gap: 1rem;
  }
  .experience__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
